export default {
	data() {
		return {
			dynamic1: true,
			dynamic2: false,
			dynamic3: false,
			type: 0,
			appoint: {},
			ImgUrl: this.ImgUrl,
			bigCalendar: this.ImgUrl + "/static/mine/bigCalendar.png",
			bigCalendarBlack: this.ImgUrl + "/static/mine/bigCalendarBlack.png"
		}
	},
	methods: {
		//切换内容
		tapInformation(e) {
			if (e == 1) {
				this.dynamic1 = true;
				this.dynamic2 = false;
				this.dynamic3 = false;
				this.type = 0
			} else if (e == 2) {
				this.dynamic1 = false;
				this.dynamic2 = true;
				this.dynamic3 = false;
				this.type = 2
			}
			else if (e == 3) {
				this.dynamic1 = false;
				this.dynamic2 = false;
				this.dynamic3 = true;
				this.type = 3
			}
			this.getAppoint()
		},
		
		// 获取预定
		getAppoint() {
			let baseUrl = this.WebUrl
			let that = this
			this.appoint = {}
			uni.getStorage({
				key: 'u_id',
				success: (id) => {
					uni.request({
						url: baseUrl + '/getAppoint',
						data: {
							u_id: id.data,
							status: that.type
						},
						success: (res) => {
							console.log(res)
							if(res.data.msg === '请求成功!'){
								res.data.data.forEach(function(item,index){
									let month = item.date.split('-')[1]
									let day = item.date.split('-')[2]
									let data = {
										meet_name: item.meet_name,
										begin_time: item.begin_time,
										end_time: item.end_time,
										month: month,
										day: day,
										theme: item.theme,
										pass: item.pass
									}
									that.$set(that.appoint,index,data)
								})
							}
						}
					})
				}
			})
		}
	},
	onLoad() {
		this.getAppoint()
	}
}
